import React from 'react';
import classes from "./Menu.module.css"
import MainButton from '../../UI/MainButton/MainButton';

function MenuItem(props) {
    return ( 
        <article className={classes.menuItem}>
            <main className={classes.menuItemMain}>
                <img src={process.env.REACT_APP_SERVER_URL+props.product.img} alt={props.product.name} className={classes.menuItemImg} />
                <div>
                    <h2 className={classes.menuItemName}>{props.product.name}</h2>
                    <p className={classes.menuItemDirection}>{props.product.description}</p>
                </div>
            </main>
            <footer className={classes.menuItemFooter}>
                <div className={classes.menuItemCost}>от {props.product.costs[0]?.cost} ₽</div>
                <MainButton>В корзину</MainButton>
            </footer>
        </article>
     );
}

export default MenuItem;