import React, { useState } from 'react';
import Modal from '../../UI/Modal/Modal';
import FormCreateProduct from './FormCreatePorduct';
import FormCreateCategory from './FormCreateCategory';
import Navbar from '../../UI/Navbar/Navbar';
import withAuth from '../../HOC/withAuth';

function AdminPanel({lox}) {
    const [productIsOpen, setProductIsOpen] = useState(false)
    const [categiryIsOpen, setCategoryIsOpen] = useState(false)

    return ( 
        <section>
            <Navbar>
                <a onClick={()=>setProductIsOpen(true)}>Продукт</a>
                <a onClick={()=>setCategoryIsOpen(true)}>Категория</a>
            </Navbar>

            <Modal  visible={productIsOpen} setVisible={setProductIsOpen}>
                <FormCreateProduct></FormCreateProduct>
            </Modal>


            <Modal setVisible={setCategoryIsOpen}>
                <FormCreateCategory></FormCreateCategory  >
            </Modal>
            

        </section>
     );
}

export default withAuth(AdminPanel);