import React from 'react';
import classes from '../../../UI/List/List.module.css'
import MainButton from '../../../UI/MainButton/MainButton';
function CostItem({size, cost, deleteCost}) {
    return (  
        <li className={classes.listElement}>
            <table>
                <thead></thead>
                <tbody>
                    <tr>
                        <td>Размер:</td>
                        <td>{size}</td>
                    </tr>
                    <tr>
                        <td>Цена:</td>
                        <td>{cost}</td>
                    </tr>    
                </tbody>
                <tfoot></tfoot>
            </table>
            <MainButton onClick={deleteCost}>Удалить</MainButton> 
        </li>
    );
}
export default CostItem;