import React from 'react';

import imgDel from '../../img/delivery.png'
import classes from './MainHeader.module.css'
import Logo from '../Logo/Logo';
import NumberInfo from '../Number/NumberInfo';

function MainHeader() {
    return (  
        <header className={classes.mainHeader}>
            <div className={classes.left}>
                <Logo></Logo>
                <div className={classes.mid}>
                    <span className={classes.up}>
                        <span className={classes.name}>Доставка пасты</span>
                        <a className={classes.city}>Москва</a>
                    </span>
                    <span className={classes.down}>
                        <img src={imgDel} alt="0 "/>               
                        <span className={classes.deliveryName}>Яндекс еда</span>
                        <div className={classes.circle}> </div>
                        <span className={classes.deliveryRate}>4.8</span>
                        <span className={classes.star}>★</span>
                        <span className={classes.spanTime}>Время доставки</span>
                        <div className={classes.circle}> </div>
                        <span className={classes.time}>от 31 мин</span>
                    </span>    
                </div>
            </div>
            <NumberInfo></NumberInfo>

        </header>
    );
}

export default MainHeader;