import React from 'react';
import Label from './Label';
import Checkbox from '../Input/Checkbox';

 function LabelByCheckbox({id, placeholder="", direction = "column", children, state}) {
    return (  
        <Label direction={direction} htmlFor={id}>
            {children}
            <Checkbox placeholder={placeholder} id={id} state={state}></Checkbox>
        </Label>
    );
 }
 
 export default LabelByCheckbox;