import React from 'react';
import classes from './Contact.module.css'


function Contacts() {
    return ( 
        <section className={classes.contactList}>
            <header className={classes.contactHeader}>Остались вопросы? А мы всегда на связи:</header>
            <main className={classes.contact}>
                <a>
                    <img src="" alt="тг" />
                </a>
                <a>
                    <img src="" alt="вт" />
                </a>
                <a>
                    <img src="" alt="вб" />
                </a>
                <a>
                    <img src="" alt="вк" />
                </a>
                <a>
                    <img src="" alt="пр" />
                </a>
                <a>
                    <img src="" alt="пр" />
                </a>
                <a>
                    Написать нам
                </a>
            </main>
            
        </section>
     );
}

export default Contacts;