import axios from 'axios'

export class ProductService{
    
    apiProductURL = process.env.REACT_APP_API_URL + '/product'

    async getAllProducts(categoryId = null){
        const url = this.apiProductURL + (categoryId ? `?categoryId=${categoryId}`:"")
        try {
            const response = await axios.get(url)
            return (response.data)
        } catch (error) {
            console.log(error.message)
            return([])
        }
        
    }

    async postProduct(product){
        const data = await axios.post(this.apiProductURL, product, {'Content-Type': 'multipart/form-data'})
        console.log(data)
    }

    async getOneProduct(){
        
    }

}