import React, { useEffect, useState } from 'react';
import classes from './Menu.module.css'
import MenuItem from './MenuItem';
import { ProductService } from '../../API/ProductService';
import useFetch from '../../hooks/useFetch';
import Loader from '../../UI/Loader/Loader';


function MenuList({id,title, name}) {
    const [products, setProducts] = useState([])
    const [fetheing, isLoading, error] = useFetch(fetchProduct)

    useEffect(()=>{
        fetheing()
    },[])

    async function fetchProduct (){
        const productService = new ProductService()
        const response = await productService.getAllProducts(id)
        setProducts(response)
    }
    
    return ( 
        <section className={classes.menuList} id={name}> 
        <h2 className={classes.menuListTitle}>{title}</h2>
        <main className={classes.menuListMain}>   
        {   isLoading ?
                <Loader></Loader>
            :
                products?.map(product => {
                return <MenuItem key={product.id} product = {product}/>
        })
        }    
        </main>
        </section>
     );
}

export default MenuList;