import React from 'react';

function Select({options=[], name="", id="", state={value:"", setValue:()=>{}}}) {

    return ( 
        <select name={name} id={id} onChange={event=>state.setValue(event.target.value)} value={state.value}>
            {
                options.map((option)=>{
                    return <option key={option.id} value={option.id}>{option.title}</option>
                })
            }
        </select>
     );
}

export default Select;