import React, { useEffect, useMemo, useRef, useState } from 'react';
import Label from '../../UI/Label/Label';
import LabelByInput from '../../UI/Label/LabelByInput';
import MainButton from '../../UI/MainButton/MainButton';
import LabelByCheckbox from '../../UI/Label/LabelByCheckbox';
import Cost from './Cost/Cost';
import { CategoryService } from '../../API/CategoryService';
import Select from '../../UI/Select/Select';
import {ProductService} from '../../API/ProductService';

function FormCreateProduct() {
    
    const [name, setName] = useState('')
    const [description, setDescription] = useState('');
    const imgRef = useRef()
    const [isNew, setIsNew] = useState(true)
    const [costs, setCosts] = useState([])
    const [category, setCategory] = useState()
    const [categories, setCategories] = useState([])

    function handleSubmit(event){
        event.preventDefault()
        const formData = new FormData()
        formData.append('categoryId', category)
        formData.append('name', name)
        formData.append('description', description)
        formData.append('img', imgRef.current?.files[0])
        formData.append('isNew', isNew)
        formData.append('costs', JSON.stringify(costs))
        const productService = new ProductService()
        productService.postProduct(formData)
    }
    
    async function fetchCetegory(){
        const categoryService = new CategoryService()
        const response = await categoryService.getAll()
        setCategories(response)
    }

    useEffect(()=>{fetchCetegory()},[])
    
    return ( 
        <form action="" method="post">
            <Select options={categories.categories} state={{value:category, setValue:setCategory}}></Select>
            <LabelByInput id="name" placeholder='Пицца' state={{value: name, setValue: setName}}>Название</LabelByInput>
            <LabelByInput id="description" placeholder='Очень вкусная' direction='column' state={{value: description, setValue: setDescription}}>Описание</LabelByInput>
            <Label htmlFor="img"><input id="img" type = "file" accept='.jpg, .png, .jpeg, .svg, .bmp, .webp' ref={imgRef}></input></Label>
            <LabelByCheckbox id="isNew" direction='row' state={{value: isNew, setValue: setIsNew}} >Новый</LabelByCheckbox>
            <Cost costs={costs} setCosts={setCosts}></Cost>
            <MainButton onClick={handleSubmit}>Создать</MainButton>
            
        </form>
     );
}

export default FormCreateProduct;