import React, { useState } from 'react';
import LabelByInput from '../../UI/Label/LabelByInput';
import MainButton from '../../UI/MainButton/MainButton';
import { CategoryService } from '../../API/CategoryService';

function FormCreateCategory() {
    const [title, setTitle] = useState()
    const [name, setName] = useState()

    function handleSubmite(event){
        event.preventDefault()

        const category = {
            title,
            name
        }

        const categoryService = new CategoryService()
        categoryService.postCategory(category)
    }

    return ( 
        <form action="">
            <LabelByInput key={'title'} state={{value:title, setValue: setTitle}}>Title</LabelByInput>
            <LabelByInput key={'name'} state={{value: name, setValue: setName}}>Name</LabelByInput>
            <MainButton onClick={handleSubmite}>Создать</MainButton>
        </form>
     );
}

export default FormCreateCategory;