import React, { useState } from 'react';
import CostList from './CostList';
import MainButton from '../../../UI/MainButton/MainButton';
import LabelByInput from '../../../UI/Label/LabelByInput';

function Cost({costs, setCosts}) {

    const [size, setSize] = useState('')
    const [value, setValue] = useState('')

    function addCost(event){
        event.preventDefault()
        const newCost = {
            size,
            cost:value
        }
        const newArray = costs.filter(cost => cost.size === newCost.size)
        if(newArray.length !== 0 ){
            return
        }
        setCosts([newCost , ...costs])
    }
    
    function createDeleteCost(cost) {
        return (event)=>{
            event.preventDefault()
            setCosts(costs.filter(element => element.size !== cost.size))
        }
    }

    return ( 
        <div>
            <LabelByInput state={{value:size, setValue:setSize}}>Размер</LabelByInput>
            <LabelByInput state={{value, setValue}}>Цена</LabelByInput>
            <MainButton onClick={addCost}>Добавить цену</MainButton>
            <CostList costs={costs} deleteCost={createDeleteCost}>Цены</CostList>
        </div>
     );
}

export default Cost;

