import React from 'react';
import classes from './PaymentAndDelivery.module.css'
import InfoSection from './InfoSection/InfoSection';
function PaymentAndDelivery() {
    const linkMap = 'https://yandex.ru/maps/?um=constructor%3Af10e1d613a893516e07e381d2524b7f397b059fe6946fba6f43ed9864bebd20d&source=constructorLink'
    return (  
        <section className={classes.PaymentAndDelivery}>
            <header>Оплата и Доставка</header>
            <InfoSection></InfoSection>
            <button onClick={()=>window.open(linkMap)}>
                Зона Доставки
            </button>
        </section>
    );
}

export default PaymentAndDelivery;