import React from 'react';
import Label from './Label';
import Input from '../Input/Input';

 function LabelByInput({id, placeholder="", direction = "column", children, inputType, state}) {
    return (  
        <Label direction={direction} htmlFor={id}>
            {children}
            <Input placeholder={placeholder} id={id} type={inputType} state={state}></Input>
        </Label>
    );
 }
 
 export default LabelByInput;