import React from 'react';
import classes from './Social.module.css'
function Social() {
    return ( 
        <section className={classes.social}>
            <header>Мы в соцсетях</header>
            <main>
                <a>YouTube</a>
                <a>Instagram</a>
                <a className={classes.doubleRow}>Москва ул. Проспект Вернадского 86В</a>
                <a>Facebook</a>
                <a>ВКонтакте</a>
            </main>
        </section>
     );
}

export default Social;